import React, {useState} from 'react';
import { ReactComponent as IconFacebook } from './assets/icons/facebook.svg';
import { ReactComponent as IconInstagram } from './assets/icons/instagram.svg';
import logo from './assets/Logo.png'
import "./App.css";
import {createClient} from '@supabase/supabase-js'
import { ToastContainer, toast } from 'react-toastify';
import {
  Button,
  createTheme, Row,
  Input,
  NextUIProvider,
  Spacer,
  styled,
  Text
} from "@nextui-org/react";
import Confetti from './Confetti';
import 'react-toastify/dist/ReactToastify.css';

// Create a single supabase client for interacting with your database
const SUPABASE_URL = "https://dkmarumztnfdiimllinu.supabase.co"
const PUBLIC_ANON_KEY = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImRrbWFydW16dG5mZGlpbWxsaW51Iiwicm9sZSI6ImFub24iLCJpYXQiOjE2Njk3MzEzOTAsImV4cCI6MTk4NTMwNzM5MH0.RAg3HV9fL9bCO2FJGAsffMQgGsDd-8N5QpxjdBXcm2c"
const supabase = createClient(SUPABASE_URL, PUBLIC_ANON_KEY)

const darkTheme = createTheme({
    type: "dark",
    theme: {
        colors: {}
    }
});

const StyledApp = styled("div", {
});

export default function App() {
    const [email, setEmail] = useState("");
    const [alreadySent, setAlreadySent] = useState(false);

    let handleInputChange = event => {
        setEmail(event.target.value)
    }

    let onPress = async () => {
        const {data, error} = await supabase
            .from('emails')
            .insert({value: email})
            .select('*')
        if (data) {
            setAlreadySent(true)
        } else {
            toast.error('🐸 Well, this is embarrassing. While we fix it, why not take a break and come back to us later? Or, if you\'re feeling brave, you can try your luck again now.', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "dark",
            });
            console.log(error)
        }
    }

    return (
        <NextUIProvider theme={darkTheme}>
            <StyledApp className="App">
                <div className="card">
                    <div className="header">
                        <div className="logo">
                            <img src={logo} alt="Bönpello Logo"/>
                        </div>
                        <div className="social">
                            <a href="https://facebook.com/bonpello.es" title="Facebook en español" target="_blank"
                               rel="noopener noreferrer">
                                <IconFacebook className="icon"/>
                            </a>
                            <a href="https://www.instagram.com/bonpello.sp" title="Instagram en español"
                               target="_blank" rel="noopener noreferrer">
                                <IconInstagram className="icon"/>
                            </a>
                            <a href="https://www.instagram.com/bonpello.en" title="Instagram in English"
                               target="_blank" rel="noopener noreferrer">
                                <IconInstagram className="icon"/>
                            </a>
                        </div>
                    </div>
                    <div className="content">
                        <div className="title-holder">
                          <Row gap={0}>
                              <Text
                                  h1
                                  css={{
                                    color: "white",
                                  }}
                                  weight="bold"
                              >
                                Coming
                              </Text>
                            <Spacer h={1}/>
                              <Text
                                  h1
                                  css={{
                                    textGradient: "45deg, $blue600 -20%, $pink600 50%",
                                  }}
                                  weight="bold"
                              >
                                Soon
                              </Text>
                          </Row>
                            <p>Don't miss out on our launch. Leave your email to stay in the loop</p>
                        </div>
                        <Spacer y={1}/>
                        <Input
                            bordered
                            labelPlaceholder="Email"
                            color="secondary"
                            size="lg"
                            onChange={handleInputChange}
                            value={email}
                        />
                        <Spacer y={1}/>
                        <Button size="lg" color="gradient" auto onPress={onPress} disabled={email === "" || alreadySent}>
                            Subscribe
                        </Button>
                    </div>
                </div>
                {alreadySent && <Confetti />}
                <ToastContainer
                    position="top-right"
                    autoClose={5000}
                    hideProgressBar={false}
                    newestOnTop={false}
                    closeOnClick
                    rtl={false}
                    pauseOnFocusLoss
                    draggable
                    pauseOnHover
                    theme="dark"
                />
            </StyledApp>
            </NextUIProvider>
    );
}
